/* SignUp.css */

.signup-container {
    width: 300px;
    margin: 0 auto;
  }
  .signup-header {
    text-align: center;
    margin-bottom: 20px;
    color: #fff;
  }
  .email{
    box-sizing: border-box;
    margin-right: 10px;
    padding: 10px;
    border-radius: 5px;
    border: 1px solid #ccc;
    width: 150px;}

  .email-label{
    font-size: 20px;
  }

  .password{
    box-sizing: border-box;
    margin-right: 10px;
    padding: 10px;
    border-radius: 5px;
    border: 1px solid #ccc;
    width: 150px;
  }

.password-label{
    font-size: 20px;
  }
  .signup-button{
    background-color: green;
    color: white;
    padding: 10px 20px;
    border: gray 1px solid;
    border-radius: 5px;
    cursor: pointer;
    margin-top: 10px;
    font-size: 16px;}

    .signup-button:hover{
      background-color: #0056b3;
    }

  .signup{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: 100vh;
    background-color: #7091E6;
  }
  .signup-page{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: 100vh;
    background-color: #7091E6;
  }
  
  .signup-form {
    background-color: #f5f5f5;
    border-radius: 8px;
    padding: 20px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  }
  
  .signup-form h2 {
    text-align: center;
    margin-bottom: 20px;
  }
  
  .signup-form label {
    display: block;
    margin-bottom: 10px;
  }
  
  .signup-form input[type="email"],
  .signup-form input[type="password"] {
    width: 100%;
    padding: 10px;
    border: 1px solid #ccc;
    border-radius: 4px;
    box-sizing: border-box;
    margin-bottom: 20px;
  }
  
  .signup-form button[type="submit"] {
    width: 100%;
    padding: 10px;
    background-color: #007bff;
    color: #fff;
    border: none;
    border-radius: 4px;
    cursor: pointer;
  }
  
  .signup-form button[type="submit"]:hover {
    background-color: #0056b3;
  }
  